import React from "react"
import AboutUs from "../components/aboutUs"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { useStaticQuery, graphql } from "gatsby"

const YtbehandlingPage = () => {
    const { gcms } = useStaticQuery(graphql`
    {
        gcms {
            golvslipnings {
                hero {
                  url
                }
              }
            }
    }

  `)
    return (
        <Layout>
            <SEO title="Ytbehandling" />
            <div class="cntnr column-flex">
                <section class="ytbehandling-section">
                    <h1 class="section-header">
                        Ytbehandling
                    </h1>
                    <div class="ytbehandling-container">
                    <article>
                        <p>
                            För att en golvslipning ska uppnå optimalt resultat ska trä eller parkettgolv behandlas efter slipning. Det finns varierande golvbehandlingar som passar till olika golv.
                            </p>
                        <p>
                            - <strong>Hårdvaxolja</strong>  är baserad på naturliga oljor och vax. Den ger en vacker och mycket slitstark yta. Denna behandling kan ge både en glansig och en matt yta, och verkar avvisande mot vätskor och smuts. På så sätt får golvet en mycket slitstark ytbehandling. Hårdvaxolja passar alla träslag.
                            </p>
                        <p>
                            - <strong>Lackering</strong> är den vanligaste typen av behandling på trägolv. Det finns ett stort utbud av golvlack gällande nyanser och typ av glans. Vi använder oss av miljövänliga lack som ger en mycket slitstark yta. Vi har både vatten och - oljebaserade golvlack. Oljebaserad är en aning  slitstarkare och tåligare.
                            </p>
                        <p>
                            - <strong>Olja</strong> ger ett väldigt vackert resultat. Förutom det starka skyddet så får golvet ett silkeslent utseende. Oljan tränger in i träet för att bevara de naturliga ådringarna samtidigt som golvet kan andas. Resultatet blir ett mycket skönt golv att gå på med ett fint utseende. En oljebehandlat golv kräver dock vård för att bibehålla sin vackra och sköna yta i längden. Olja finns i många olika nyanser. Vax kan tillsättas för ytor som som ska tåla extra fukt och smuts.
                            </p>
                        <p>
                            - <strong>Lut</strong> används på furu och grangolv. Lut förhindrar att golvet gulnar och skapar istället en fräsch, vitaktig och ljus nyans. Det är vanligt att addera vitpigmenterad olja efter en lutbehandling för att åstadkomma ett ännu ljusare resultat. Lutbehandlingen gör att golvet blir mer fukttåligt, mer reptåligt och förhindrar  att golvet påverkas av bl.a. solljus. Slutbehandling med såpa ger en mattare yta, olja ger dock ett bättre skydd.
                            </p>
                        <p><em>Vi rekommenderar följande kombinationer vid lutbehandlignar:<br />
                                Lut ± vitpigmenterad olja <br />
                                Vitpigmenterad lut ± naturell olja <br />
                                Vitpigmenterad lut ± vitpigmenterad olja <br />
                                Lut + såpa med eller utan vitpigment</em>
                            </p>
                    </article>
                    <div class="ytbehandling-img-container" >
                        <img  src={gcms.golvslipnings[1].hero.url} />    
                        <img  src={gcms.golvslipnings[2].hero.url} />    
                    </div>
                    </div>
                </section>
            </div>
        </Layout>
    )
}

export default YtbehandlingPage;