import React from "react"
import { useStaticQuery, graphql } from "gatsby"

const AboutUs = ({hidden}) => {
    const { gcms: { images } } = useStaticQuery(graphql`
    {
        gcms {
          images {
            indexAboutUs {
              url
            }
          }
        }
    } 
    `)



    return (
        <div>
            <h1 className="about-us-header" style={{display:hidden?"none":""}}>
                Om oss
            </h1>
            <section class="responsive-section">
                <article class="about-us-article" style={{}}>
                    <p>Vi utför våra uppdrag med största professionalism.
                    Vår personal är välutbildad inom golvbranschen och har lång erfarenhet inom yrket.
                    Vår högsta ambition är att du som kund ska vara nöjd och att du ska känna dig trygg med oss.
                    Vi anpassar oss alltid efter dina önskemål. För oss är det viktigt att du känner dig delaktig
                    och uppdaterad under arbetets gång. Vi är inte nöjda förrän vi har lämnat ett uppdrag där du
                    som kund är hundra procent nöjd.
                </p>
                    <p>
                        Victoria Golv  är verksamma i Stockholmsområdet och riktar oss mot såväl privatpersoner som företag.
                </p>
                </article>
                <img width="50%" src={images[0].indexAboutUs.url} alt="person lägger golv" />
            </section>
        </div>
    )
}

export default AboutUs;